import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";

import notification from "../../../../components/notification";
import { Empty, Input, Tooltip } from "antd";

import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/customer/customerType/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { Box, StatusTag } from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange"
// const Panel = Collapse.Panel;
// const Option = Select.Option;
const Search = Input.Search;

const redirectTo = "/dashboard/setup/menus";

class CustomerType extends Component {
  state = {
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getCustomerTypes } = this.props;
    getCustomerTypes();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.customerType.error) {
      notification("error", this.props.customerType.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.customerType.success) {
      notification("success", this.props.customerType.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { getCustomerTypes } = this.props;

    getCustomerTypes(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;

    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getCustomerTypes } = this.props;
    // this.setState({
    //   filter: {
    //     id_cash_flow_group: [],
    //     transaction_type: [],
    //     pagination: 10
    //   }
    // });
    this.setState({ search: null });
    getCustomerTypes();
  };

  // Handle filter change
  // handleFilterChange = (name, value) => {
  //   this.setState({
  //     filter: {
  //       ...this.state.filter,
  //       [name]: value
  //     }
  //   });
  // };

  // Handle filter
  // handleFilter = () => {
  //   const { getCustomerTypes } = this.props;
  //   //const { current_page } = this.props.customerType;
  //   const { filter } = this.state;
  //   let string = "";

  //   if (filter.transaction_type) {
  //     string += "transaction_type=" + filter.transaction_type;
  //   }
  //   if (filter.pagination) {
  //     string
  //       ? (string += "&pagination=" + filter.pagination)
  //       : (string += "pagination=" + filter.pagination);
  //   }

  //   this.setState({ search: null });
  //   getCustomerTypes(1, null, string);
  // };

  // Handle Reset Filter
  // handleResetFilter = () => {
  //   const { getCustomerTypes } = this.props;
  //   this.setState({
  //     filter: {
  //       //id_cash_flow_group: [],
  //       transaction_type: [],
  //       pagination: 10
  //     }
  //   });
  //   this.setState({ search: null });
  //   getCustomerTypes();
  // };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateCustomerType(id);
    } else {
      this.props.deactivateCustomerType(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getCustomerTypes } = this.props;
    const { search } = this.props.customerType;
    // const { filter } = this.props.customerType;
    getCustomerTypes(page, search);
  };

  listAction = {
    title: "",
    dataIndex: "id_customer_type",
    rowKey: "id_customer_type",
    width: "10%",
    align: "center",
    render: (text, row) => (
      <div>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button size="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>

        {row.is_active ? (
          <Popconfirms
            title={<IntlMessages id="customerTypes.popconfirm.deactivate" />}
            onConfirm={this.handleRecord.bind(this, "deactivate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.deactivate" />}
            >
              <Button size="small" className="invoiceDltBtn">
                <i className="ion-close-circled" />
              </Button>
            </Tooltip>
          </Popconfirms>
        ) : (
            <Popconfirms
              title={<IntlMessages id="customerTypes.popconfirm.activate" />}
              onConfirm={this.handleRecord.bind(this, "activate", text)}
              okText={getLanguage({ id: "yes" })}
              cancelText={getLanguage({ id: "no" })}
            >
              <Tooltip
                placement="topRight"
                title={<IntlMessages id="tooltip.activate" />}
              >
                <Button size="small" className="invoiceRstBtn">
                  <i className="ion-refresh" />
                </Button>
              </Tooltip>
            </Popconfirms>
          )}
      </div>
    )
  };

  render() {
    // console.log(this.state.filter);
    const columns = [
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "customer_type_code",
        rowKey: "customer_type_code",
        width: "10%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "name" }),
        dataIndex: "customer_type_name",
        rowKey: "customer_type_name",
        width: "15%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "label.description" }),
        dataIndex: "description",
        rowKey: "description",
        width: "15%",
        render: display => {
          if (display) {
            if (display.length > 50) {
              return (
                <Tooltip placement="bottom" title={display}>
                  {display.substring(0, 50) + "..."}
                </Tooltip>
              )
            } else {
              return display;
            }
          } else {
            return "";
          }
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "is_active",
        rowKey: "is_active",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          if (text === 1) {
            className = "active";
            display = getLanguage({ id: "active" });
          } else {
            className = "nonactive";
            display = getLanguage({ id: "inactive" });
          }
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];

    // Data setup
    const { customerTypes } = this.props.customerType;
    //console.log(this.props.customerType); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //console.log(permissions);
    if (
      permissions.includes("Update Customer Type") &&
      columns.length === 4
    ) {
      columns.push(this.listAction);
    }

    Object.keys(customerTypes).map((customerType, index) => {
      return dataSource.push({
        ...customerTypes[customerType],
        key: customerType
      });
    });

    // Pagination Setup
    const { current_page, page_size, total } = this.props.customerType;

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="customerTypes.pageHeader" />}
            marginBottom={true}
          >
            <div style={{ display: "flex", justifyContent: "flex-end" }}>
              {permissions.includes("Create Customer Type") ? (
                <Link to={`${this.props.match.path}/add`}>
                  <Button type="primary" className="mateAddInvoiceBtn">
                    <IntlMessages id="customerTypes.button.add" />
                  </Button>
                </Link>
              ) : null}
              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={getLanguage({ id: "code" }) + ", " + getLanguage({ id: "name" })}
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <CardWrapper>
            {customerTypes.length === 0 && !this.props.customerType.loading ? (
              <Empty
                description={<IntlMessages id="customerTypes.table.noData" />}
              />
            ) : (
                <div className="isoInvoiceTable">
                  <Scrollbars style={{ width: "100%" }}>
                    <TableWrapper
                      dataSource={dataSource}
                      columns={columns}
                      loading={this.props.customerType.loading}
                      className="invoiceListTable"
                      bordered={true}
                      // Set pagination, jika ada page_size mengunakan pagination API
                      // jika tidak ada page_size menggunakan pagination bawaan
                      pagination={
                        page_size
                          ? {
                            current: current_page,
                            pageSize: parseInt(page_size),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                          : true
                      }
                    />
                  </Scrollbars>
                </div>
              )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    customerType: state.customerType,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CustomerType);
