const actions = {
  GET_CUSTOMER_TYPES_REQUEST: "GET_CUSTOMER_TYPES_REQUEST",
  GET_CUSTOMER_TYPES_SUCCESS: "GET_CUSTOMER_TYPES_SUCCESS",
  GET_CUSTOMER_TYPES_ERROR: "GET_CUSTOMER_TYPES_ERROR",
  STORE_CUSTOMER_TYPE_REQUEST: "STORE_CUSTOMER_TYPE_REQUEST",
  STORE_CUSTOMER_TYPE_SUCCESS: "STORE_CUSTOMER_TYPE_SUCCESS",
  STORE_CUSTOMER_TYPE_ERROR: "STORE_CUSTOMER_TYPE_ERROR",
  EDIT_CUSTOMER_TYPE_REQUEST: "EDIT_CUSTOMER_TYPE_REQUEST",
  EDIT_CUSTOMER_TYPE_SUCCESS: "EDIT_CUSTOMER_TYPE_SUCCESS",
  EDIT_CUSTOMER_TYPE_ERROR: "EDIT_CUSTOMER_TYPE_ERROR",
  UPDATE_CUSTOMER_TYPE_REQUEST: "UPDATE_CUSTOMER_TYPE_REQUEST",
  UPDATE_CUSTOMER_TYPE_SUCCESS: "UPDATE_CUSTOMER_TYPE_SUCCESS",
  UPDATE_CUSTOMER_TYPE_ERROR: "UPDATE_CUSTOMER_TYPE_ERROR",
  SHOW_CUSTOMER_TYPE_REQUEST: "SHOW_CUSTOMER_TYPE_REQUEST",
  SHOW_CUSTOMER_TYPE_SUCCESS: "SHOW_CUSTOMER_TYPE_SUCCESS",
  SHOW_CUSTOMER_TYPE_ERROR: "SHOW_CUSTOMER_TYPE_ERROR",
  DEACTIVATE_CUSTOMER_TYPE_REQUEST: "DEACTIVATE_CUSTOMER_TYPE_REQUEST",
  DEACTIVATE_CUSTOMER_TYPE_SUCCESS: "DEACTIVATE_CUSTOMER_TYPE_SUCCESS",
  DEACTIVATE_CUSTOMER_TYPE_ERROR: "DEACTIVATE_CUSTOMER_TYPE_ERROR",
  ACTIVATE_CUSTOMER_TYPE_REQUEST: "ACTIVATE_CUSTOMER_TYPE_REQUEST",
  ACTIVATE_CUSTOMER_TYPE_SUCCESS: "ACTIVATE_CUSTOMER_TYPE_SUCCESS",
  ACTIVATE_CUSTOMER_TYPE_ERROR: "ACTIVATE_CUSTOMER_TYPE_ERROR",
  CLEAR_SUCCESS: "CLEAR_SUCCESS",
  CLEAR_ERROR: "CLEAR_ERROR",
  CLEAR_ERROR_FIELD: "CLEAR_ERROR_FIELD",
  getCustomerTypes: (page, search) => ({
    type: "GET_CUSTOMER_TYPES_REQUEST",
    payload: {
      page: page,
      search: search
    }
  }),
  storeCustomerType: data => ({
    type: "STORE_CUSTOMER_TYPE_REQUEST",
    payload: { body: data }
  }),
  editCustomerType: id_customer_type => ({
    type: "EDIT_CUSTOMER_TYPE_REQUEST",
    payload: { id_customer_type: id_customer_type }
  }),
  updateCustomerType: (id_customer_type, data) => ({
    type: "UPDATE_CUSTOMER_TYPE_REQUEST",
    payload: {
      id_customer_type: id_customer_type,
      body: data
    }
  }),
  showCustomerType: id_customer_type => ({
    type: "SHOW_CUSTOMER_TYPE_REQUEST",
    payload: { id_customer_type: id_customer_type }
  }),
  deactivateCustomerType: id_customer_type => ({
    type: "DEACTIVATE_CUSTOMER_TYPE_REQUEST",
    payload: { id_customer_type }
  }),
  activateCustomerType: id_customer_type => ({
    type: "ACTIVATE_CUSTOMER_TYPE_REQUEST",
    payload: { id_customer_type }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
